* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  // cursor: none;
}
html {
  font-size: 62.5%;
  @include media(sm) {
    font-size: 39.51%;
  }
  @include media(md) {
    font-size: 46%;
  }
  @include media(lg) {
    font-size: 58.25%;
  }
  @include media(xxlg) {
    font-size: 64.5%;
  }
}
body {
  box-sizing: border-box;
  font-family: $main__font;
  color: $body;
}
a {
  color: $body;
}
span {
  font-weight: 700;
}
header,
.hero__content,
.describe,
.feature,
footer,
nav,
.showcase {
  @include media(xxlg) {
    max-width: 150em;
    margin: 0 auto;
  }
}
